import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { Observable } from 'rxjs';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class JournalEntryService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
  }
  public bmlfonGetList(headerData: any): Observable<ODataModel> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetLookupData`;
    return this.httpExt.post<ODataModel>(url, headerData, null);
  }

  public bmlfonSaveData(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/CreateJournalEntry`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public bmlfonGetData(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetJournalEntry`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonUpdateData(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateJournalEntry`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonDeleteData(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteJournalEntry`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonPostData(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/PostJournalEntry`;
    return this.httpExt.post(url, reqdata, null);
  }
  public bmlfonVoidData(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/VoidJournalEntry`;
    return this.httpExt.post(url, reqdata, null);
  }
  public bmlfonHoldData(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/HoldJournalEntry`;
    return this.httpExt.post(url, reqdata, null);
  }
  public bmlfonReleaseData(reqdata) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/ReleaseJournalEntry`;
    return this.httpExt.post(url, reqdata, null);
  }

  public bmlfonGetFinanceSeries(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/GetFinanceSeries`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public bmlfonSaveGridSetting(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFSaveUpdateGridSetting`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public bmlfonResetGridSetting(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFResetGridSetting`;
    return this.httpExt.post<any>(url, reqdata, null);
  }
}

