import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { Observable } from 'rxjs';
import { ODataModel } from 'src/app/core/models/odata.model';

@Injectable({
  providedIn: 'root'
})
export class ModuleSetupService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) { }

  public bmlfGetSetupdData() {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetDefaultsForOnline`;
    return this.httpExt.get(url, null);
  }

  public bmlfSaveSetupData(reqData) : Observable<any>{
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveSetupData`;
    return this.httpExt.post<any>(url , reqData, null)
  }

  public bmlfGetProductionSetup(reqData) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetProductionSetup`;
    return this.httpExt.post(url, reqData, null);
  }

  public bmlfSaveProductionSetup(reqData) : Observable<any>{
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFSaveProductionSetup`;
    return this.httpExt.post<any>(url , reqData, null)
  }
}
